.routine-create {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.routine-create-box {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  position: relative;

  width: auto;
}

.close-button {
  position: absolute;
  top: 1px;
  right: 3px;
  background: none;
  border: none;
  font-size: 1.5em;
  cursor: pointer;
  padding: 0 5px 0 0;
}

.routine-create-content {
  width: 200px;
  display: flex;
}

.routine-create-category-box {
  display: flex;
  justify-content: left;
  width: 100px;
  gap: 0;
  padding: 10px;
}

.routine-create-button-box {
  display: flex;
  justify-content: center;
  align-items: center;
}

.routine-create-button {
  width: 100px;
  height: 40px;
  justify-self: flex-end;

  border-radius: 5px;
  background-color: rgba(0, 0, 255, 0.3);

  cursor: pointer;
}
